.main-wrapper-card{
    display: flex;
    gap: 50px;
    flex-wrap: wrap;
    .card-approval-line{
        border:1px solid black;
        width: 350px;
        height: 400px;
        border-radius:20px ;
        .card-header{
            text-align: center;
            padding-top: 8px;
            padding-bottom: 8px;
            border-bottom:1px solid black;
        }
        .card-body{
            padding:10px 0px 0px 16px;
            height: 300px;
        }
        .card-footer{
            display: flex;
            justify-content: space-between;
            padding: 10px;
            button{
                background-color: rgb(255, 183, 89);
                padding: 10px;
                width: 100px;
    
            }
        }
    }
}

